.slider {
  position: absolute;
  bottom: 0;
  right: 0;
  background-image: url(../images/slider/slide-img-1.png);
  width: 687px;
  height: 680px;
  border-radius: 200px 0 0;

  &__info {
    font-family: $fontOpenSans;
    position: absolute;
    bottom: 48px;
    left: 144px;

  }

  &__info-tittle {
    font-family: $fontPoppins;
    font-size: 13px;
    line-height: 19px;

    letter-spacing: 3px;
    text-transform: uppercase;
    color: $text-color;

  }

  &__info-text {
    font-size: 14px;
    line-height: 140%;
    font-weight: normal;
    margin-top: 8px;
    color: $text-color;

  }
  .arrow {
    width: 32px;
    height: 32px;
    background-color: white;
    border-radius: 50%;
    position: absolute;
    text-align: center;
    transition: transform $hover-duration , background-color $hover-duration;

    &:hover {
      transform: scale(1.2);
      background-color: rgb(230, 137, 137);

    }
  }

  &__arrow-left {
    bottom: 56px;
    left: 55px;
  }

  &__arrow-right {
    bottom: 56px;
    left: 95px;
  }
}
